.conteiner {
  padding: 20px;
}

.backBtn {
  text-decoration: none;
  margin-left: 20px;
  margin-bottom: 10px;
  padding: 5px 10px 5px 10px;

  border-radius: 4px;
  color: white;
  background-color: rgb(159, 0, 255);
  transition: 500ms background-color linear;
}

.backBtn:hover,
.backBtn:focus {
  background-color: rgb(0 154 255);
  transition: 500ms background-color linear;
}
